.home{
    width:100%;
    @include flex_dlc(column,center,center)
}

.physical-data{
    &-ctn{
        width:60%;
        min-width:1400px;
        margin: 25px 10px 25px 10px; 
        @include flex_dlc(row,flex-start,space-around)
    }
    &-general{
        @include flex_dlc(column,flex-start,center)
    }
    &-incline{
        width:500px;
        @include flex_dlc(column,flex-start,flex-start);
        &-diagram{
            width:100%;
            @include flex_dlc(row,center,center);
        }
    }
    &-orbital{
        width:60%;
        min-width:1400px;
        height: auto;
        margin: 25px 10px 25px 10px; 
        @include flex_dlc(column,flex-start,center);
        &-title{
            &-mobile{
                display: none;
            }
        }
        &-diagram{
            width:100%;
            height: auto;
            @include flex_dlc(row,center,center);
        }
    }
}



@media screen and (max-width:1400px){
    .physical-data{
        &-ctn{
            width: 100%;
            height: auto;
            min-width: 0;
        }
        &-orbital{
            width: 100%;
            height: 1000px;
            min-width: 0;
        }
    }
}

@media screen and (max-width:1200px){
    .physical-data{
        &-orbital{           
            height: 800px;            
        }
    }
}

@media screen and (max-width:991px){
    .physical-data{
        &-ctn{
            flex-direction: column;
        }
        &-incline{
            width:100%;
        }
        &-orbital{            
            height: 600px;
            min-width: 0;
            &-title{
                &-mobile{
                    display: block;
                }
                &-desktop{
                    display:none;
                }
            }
        }
    }
}